<template>
  <div class="user-management">
    <!-- 顶部搜索栏 -->
    <div class="header">
      <div class="header-left">
        <h2>用户管理</h2>
        <span class="user-count">共 {{ users.length }} 个用户</span>
      </div>
      <div class="search-box">
        <i class="fas fa-search"></i>
        <input 
          v-model="searchQuery" 
          placeholder="搜索用户名或邮箱..."
        >
      </div>
    </div>

    <!-- 加载状态 -->
    <div v-if="loading" class="loading-state">
      <div class="spinner"></div>
      <span>加载中...</span>
    </div>

    <!-- 错误提示 -->
    <div v-if="error" class="error-message">
      <i class="fas fa-exclamation-circle"></i>
      <span>{{ error }}</span>
      <button @click="loadUsers" class="retry-btn">
        <i class="fas fa-redo"></i>
        重试
      </button>
    </div>

    <!-- 用户列表 -->
    <div v-else class="users-grid">
      <div v-for="user in filteredUsers" :key="user._id" class="user-card">
        <!-- 用户基本信息 -->
        <div class="user-card-header">
          <div class="user-info">
            <div class="avatar">
              <i class="fas fa-user"></i>
            </div>
            <div class="user-details">
              <h3>{{ user.username }}</h3>
              <span class="email">{{ user.email }}</span>
              <span class="last-login-ip" v-if="user.lastLoginIp">
                <i class="fas fa-network-wired"></i>
                最后登录IP: {{ user.lastLoginIp }}
              </span>
            </div>
          </div>
          <div class="user-status">
            <span 
              class="status-badge"
              :class="getStatusClass(user.status)"
            >
              {{ user.status || '正常' }}
            </span>
          </div>
        </div>

        <!-- 用户数据概览 -->
        <div class="user-stats">
          <div class="stat-item">
            <i class="fas fa-wallet"></i>
            <div class="stat-content">
              <span class="stat-label">余额</span>
              <span class="stat-value">¥{{ formatAmount(user.balance) }}</span>
            </div>
          </div>
          <div class="stat-item">
            <i class="fas fa-calendar-alt"></i>
            <div class="stat-content">
              <span class="stat-label">注册时间</span>
              <span class="stat-value">{{ formatDate(user.createdAt) }}</span>
            </div>
          </div>
        </div>

        <!-- 操作按钮 -->
        <div class="user-actions">
          <button 
            class="action-btn"
            :class="{ 'active': selectedUser?._id === user._id }"
            @click="toggleUserDetails(user)"
          >
            <i :class="selectedUser?._id === user._id ? 'fas fa-chevron-up' : 'fas fa-chevron-down'"></i>
            {{ selectedUser?._id === user._id ? '收起详情' : '查看详情' }}
          </button>
          <button 
            class="action-btn primary"
            @click="showBalanceModal(user)"
          >
            <i class="fas fa-coins"></i>
            调整余额
          </button>
          <button 
            class="action-btn"
            :class="{ 'danger': !user.status }"
            @click="toggleUserStatus(user)"
          >
            <i :class="user.status ? 'fas fa-ban' : 'fas fa-check-circle'"></i>
            {{ user.status ? '封禁' : '解封' }}
          </button>
        </div>

        <!-- 展开的详情面板 -->
        <div 
          v-if="selectedUser?._id === user._id" 
          class="details-panel"
        >
          <!-- 通道信息 -->
          <div class="details-section">
            <h4>
              <i class="fas fa-random"></i>
              通道设置
            </h4>
            <div class="channels-grid">
              <div v-for="channel in user.channels" :key="channel.id" class="channel-card">
                <div class="channel-header">
                  <span class="channel-name">{{ channel.name }}</span>
                  <span 
                    class="channel-status"
                    :class="{ 'enabled': channel.status === '启用' }"
                  >
                    {{ channel.status }}
                  </span>
                </div>
                <div class="channel-body">
                  <div class="channel-info">
                    <span class="label">通道ID</span>
                    <span class="value">{{ channel.identifier }}</span>
                  </div>
                  <div class="channel-info">
                    <span class="label">支付参数</span>
                    <span class="value">{{ channel.customParams?.userId || '未设置' }}</span>
                  </div>
                  <div class="channel-info">
                    <span class="label">累计金额</span>
                    <span class="value highlight">¥{{ formatAmount(channel.totalAmount) }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- 交易记录 -->
          <div class="details-section">
            <h4>
              <i class="fas fa-history"></i>
              交易记录
            </h4>
            <div class="transactions-list">
              <div v-for="tx in user.transactions" :key="tx._id" class="transaction-item">
                <div class="tx-icon" :class="getTxTypeClass(tx.type)">
                  <i :class="getTxIcon(tx.type)"></i>
                </div>
                <div class="tx-info">
                  <div class="tx-header">
                    <span class="tx-type">{{ tx.type }}</span>
                    <span class="tx-time">{{ formatDate(tx.createdAt) }}</span>
                  </div>
                  <div class="tx-details">
                    <span class="tx-amount" :class="{ 'positive': tx.amount > 0, 'negative': tx.amount < 0 }">
                      {{ tx.amount > 0 ? '+' : '' }}{{ formatAmount(tx.amount) }}
                    </span>
                    <span class="tx-balance">余额: ¥{{ formatAmount(tx.balance) }}</span>
                  </div>
                  <div class="tx-desc" v-if="tx.description">
                    {{ tx.description }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 余额调整弹窗 -->
    <div v-if="showBalanceDialog" class="modal-overlay">
      <div class="modal" @click.stop>
        <div class="modal-header">
          <div class="header-content">
            <h3>调整余额</h3>
            <div class="user-brief">
              <span class="username">{{ selectedUser?.username }}</span>
              <span class="current-balance">当前余额: ¥{{ formatAmount(selectedUser?.balance) }}</span>
            </div>
          </div>
          <button class="close-btn" @click="closeBalanceModal">
            <i class="fas fa-times"></i>
          </button>
        </div>
        
        <div class="modal-content">
          <div class="adjustment-type">
            <button 
              :class="['type-btn', { active: balanceAdjustment.type === '增加' }]"
              @click="balanceAdjustment.type = '增加'"
            >
              <i class="fas fa-plus-circle"></i>
              增加
            </button>
            <button 
              :class="['type-btn', { active: balanceAdjustment.type === '减少' }]"
              @click="balanceAdjustment.type = '减少'"
            >
              <i class="fas fa-minus-circle"></i>
              减少
            </button>
          </div>

          <div class="input-group">
            <label>金额</label>
            <div class="amount-input">
              <span class="currency">¥</span>
              <input 
                type="number" 
                v-model="balanceAdjustment.amount"
                min="0"
                step="0.01"
                placeholder="请输入调整金额"
              >
            </div>
          </div>

          <div class="input-group">
            <label>说明</label>
            <input 
              type="text" 
              v-model="balanceAdjustment.description"
              placeholder="请输入调整原因"
            >
          </div>

          <div class="preview-section">
            <div class="preview-item">
              <span class="label">调整后余额</span>
              <span class="value" :class="{ 'negative': getAdjustedBalance < 0 }">
                ¥{{ formatAmount(getAdjustedBalance) }}
              </span>
            </div>
          </div>
        </div>

        <div class="modal-footer">
          <button class="cancel-btn" @click="closeBalanceModal">取消</button>
          <button 
            class="confirm-btn"
            :class="{ 'danger': balanceAdjustment.type === '减少' }"
            @click="confirmBalanceAdjustment"
            :disabled="!isValidAdjustment || getAdjustedBalance < 0"
          >
            确认{{ balanceAdjustment.type }}
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserManagement',
  data() {
    return {
      users: [],
      loading: false,
      error: null,
      searchQuery: '',
      expandedUser: null,
      showBalanceDialog: false,
      selectedUser: null,
      balanceAdjustment: {
        type: '增加',
        amount: '',
        description: ''
      },
      showDetailsDialog: false,
    }
  },
  computed: {
    filteredUsers() {
      if (!this.searchQuery) return this.users;
      
      const query = this.searchQuery.toLowerCase();
      return this.users.filter(user => 
        user.username.toLowerCase().includes(query) ||
        user.email.toLowerCase().includes(query)
      );
    },
    isValidAdjustment() {
      const amount = parseFloat(this.balanceAdjustment.amount);
      return amount > 0 && this.balanceAdjustment.description;
    },
    getAdjustedBalance() {
      if (!this.selectedUser || !this.balanceAdjustment.amount) return this.selectedUser?.balance || 0;
      const amount = parseFloat(this.balanceAdjustment.amount) || 0;
      return this.selectedUser.balance + (this.balanceAdjustment.type === '减少' ? -amount : amount);
    }
  },
  methods: {
    async loadUsers() {
      try {
        this.loading = true;
        this.error = null;
        
        const userRole = localStorage.getItem('userRole');
        const token = localStorage.getItem('token');
        
        
        if (!token || userRole !== 'superadmin') {
          throw new Error('权限不足或未登录');
        }
        
        const response = await this.$api.get('/api/users/list');
        this.users = response.data;
      } catch (error) {
        console.error('Load users error:', error);
        
        if (error.response?.status === 401) {
          localStorage.clear();
          
          try {
            await this.$router.replace('/');
            this.$message.error('登录已过期，请重新登录');
          } catch (routerError) {
            if (routerError.name !== 'NavigationDuplicated') {
              console.error('Router navigation error:', routerError);
            }
          }
          return;
        }
        
        this.error = error.message || '加载用户列表失败';
        this.$message.error(this.error);
      } finally {
        this.loading = false;
      }
    },
    async updateUserRole(userId, role) {
      try {
        await this.$api.put(`/users/${userId}/role`, { role });
        this.$message.success('更新用户角色成功');
      } catch (error) {
        console.error('Update role error:', error);
        this.$message.error('更新用户角色失败');
        await this.loadUsers(); // 重新加载用户列表
      }
    },
    formatAmount(amount) {
      return parseFloat(amount).toFixed(2);
    },
    formatDate(date) {
      return new Date(date).toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    getStatusClass(status) {
      return {
        'status-normal': !status,
        'status-banned': status === '已封禁'
      };
    },
    getTxTypeClass(type) {
      return {
        'tx-recharge': type === '充值',
        'tx-withdraw': type === '提现',
        'tx-income': type === '收入',
        'tx-expense': type === '支出'
      };
    },
    async toggleUserDetails(user) {
      if (this.selectedUser?._id === user._id) {
        this.selectedUser = null;
        return;
      }
      
      try {
        // 加载用户的通道设置和交易记录
        const [channelsRes, transactionsRes] = await Promise.all([
          this.$api.get(`/api/users/${user._id}/channels`),
          this.$api.get(`/api/users/${user._id}/transactions`)
        ]);
        
        // 更新用户数据
        const userIndex = this.users.findIndex(u => u._id === user._id);
        if (userIndex !== -1) {
          const channels = channelsRes.data.map(channel => ({
            ...channel,
            customParams: {
              ...channel.customParams,
              userId: channel.customParams?.userId || '未设置'
            }
          }));
          
          this.$set(this.users[userIndex], 'channels', channels);
          this.$set(this.users[userIndex], 'transactions', transactionsRes.data);
          this.selectedUser = this.users[userIndex];
        }
      } catch (error) {
        console.error('Load user details error:', error);
        this.$message.error('加载用户详情失败');
      }
    },
    async toggleUserStatus(user) {
      try {
        const newStatus = user.status ? null : '已封禁';
        await this.$api.put(`/api/users/${user._id}/status`, { status: newStatus });
        
        // 更新本地用户状态
        const userIndex = this.users.findIndex(u => u._id === user._id);
        if (userIndex !== -1) {
          this.$set(this.users[userIndex], 'status', newStatus);
        }
        
        this.$message.success(newStatus ? '用户已封禁' : '用户已解封');
      } catch (error) {
        this.$message.error('操作失败');
        console.error('Toggle user status error:', error);
      }
    },
    showBalanceModal(user) {
      this.selectedUser = user;
      this.balanceAdjustment = {
        type: '增加',
        amount: '',
        description: ''
      };
      this.showBalanceDialog = true;
    },
    closeBalanceModal() {
      this.showBalanceDialog = false;
      this.selectedUser = null;
      this.balanceAdjustment = {
        type: '增加',
        amount: '',
        description: ''
      };
    },
    async confirmBalanceAdjustment() {
      try {
        const amount = parseFloat(this.balanceAdjustment.amount);
        const adjustedAmount = this.balanceAdjustment.type === '减少' ? -amount : amount;
        
        await this.$api.post(`/api/users/${this.selectedUser._id}/balance`, {
          amount: adjustedAmount,
          description: this.balanceAdjustment.description
        });
        
        // 更新本地用户余额
        const userIndex = this.users.findIndex(u => u._id === this.selectedUser._id);
        if (userIndex !== -1) {
          this.$set(this.users[userIndex], 'balance', 
            parseFloat(this.users[userIndex].balance) + adjustedAmount
          );
        }
        
        this.$message.success('余额调整成功');
        this.closeBalanceModal();
      } catch (error) {
        this.$message.error('余额调整失败');
        console.error('Balance adjustment error:', error);
      }
    },
    getTxIcon(type) {
      const icons = {
        '充值': 'fas fa-plus-circle',
        '提现': 'fas fa-minus-circle',
        '收入': 'fas fa-arrow-down',
        '支出': 'fas fa-arrow-up',
        '管理员充值': 'fas fa-user-shield',
        '管理员扣除': 'fas fa-user-slash'
      };
      return icons[type] || 'fas fa-exchange-alt';
    },
    async showUserDetails(user) {
      try {
        // 加载用户的通道设置和交易记录
        const [channelsRes, transactionsRes] = await Promise.all([
          this.$api.get(`/api/users/${user._id}/channels`),
          this.$api.get(`/api/users/${user._id}/transactions`)
        ]);
        
        // 更新用户数据
        const userIndex = this.users.findIndex(u => u._id === user._id);
        if (userIndex !== -1) {
          const channels = channelsRes.data.map(channel => ({
            ...channel,
            customParams: {
              ...channel.customParams,
              userId: channel.customParams?.userId || '未设置'
            }
          }));
          
          this.$set(this.users[userIndex], 'channels', channels);
          this.$set(this.users[userIndex], 'transactions', transactionsRes.data);
        }
        
        // 设置选中的用户并显示弹窗
        this.selectedUser = this.users[userIndex];
        this.showDetailsDialog = true;
      } catch (error) {
        console.error('Load user details error:', error);
        this.$message.error('加载用户详情失败');
      }
    },
    closeDetailsModal() {
      this.showDetailsDialog = false;
      this.selectedUser = null;
    }
  },
  created() {
    const userRole = localStorage.getItem('userRole');
    
    if (userRole !== 'superadmin') {
      this.$message.error('权限不足');
      this.$router.push('/channel-market');
      return;
    }
    
    this.loadUsers();
  }
}
</script>

